import React from 'react'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'

import './style.css'

export const HeaderList = ({ view, viewStyle, handleViewFavoritesBeers, searchBeers, getBeers, numPageUser, userGoHome }) => {
  const statusButton = view ? 'active' : 'disabled'

  const handleChangeInput = async (event) => {
    let beerName = ''
    let beerDate = ''
    event.persist()
    await new Promise((resolve) => {
      if (event.target.id === 'beer_name') {
        beerName = event.target.value
      }

      if (event.target.id === 'beer_date') {
        beerDate = event.target.value
      }
      searchBeers(beerName, beerDate)
      resolve()
    })
  }

  return (
        <div className={'header__container'}>
            <div className={'user__options'}>
                <div className={'logo'} onClick={() => userGoHome()}>
                    <img src={'data:image/jpeg;base64,\n'} alt={'citibox_logo'}/>
                    <span>Beers</span>
                </div>
                <div className={'user_options_view'}>
                    <ButtonGroup
                        orientation="vertical"
                        color="primary"
                        aria-label="vertical contained primary button group"
                        variant="text"
                    >
                        <Button onClick={() => viewStyle('list')} className={statusButton}>Beers List</Button>
                        <Button onClick={() => viewStyle('cards')} className={statusButton}>Beers Cards</Button>
                        <Button onClick={() => handleViewFavoritesBeers()}>My Favorites Beers</Button>
                    </ButtonGroup>
                </div>
            </div>
            <div className={`search__container ${statusButton}`}>
                <form noValidate autoComplete="off" >
                    <input type={'text'} id="beer_name" label="Name" placeholder={'Name Beer'} onChange={handleChangeInput} />
                    <input type={'text'} id="beer_date" label="mm-yyyy" placeholder={'Beer Date (mm-yyyy)'} onChange={handleChangeInput} />
                    <input type="reset" defaultValue="Reset" onClick={() => getBeers(numPageUser)} />
                </form>
            </div>
        </div>
  )
}
